:root { 
  --black: #000000;
  --deep-cerulean: #0077a7;
  --shiraz: #ac042b;
  --white: #ffffff;
 
  --font-size-l2: 30px;
  --font-size-m2: 24px;
  --font-size-xxl: 36px;
  --font-size-xxxl: 48px;
  --font-size-xxxl2: 64px;
 
  --font-family-black_han_sans: "Black Han Sans", Helvetica;
}
.blackhansans-normal-white-36px {
  color: var(--white);
  font-family: var(--font-family-black_han_sans);
  font-size: var(--font-size-xxl);
  font-style: normal;
  font-weight: 400;
}

.blackhansans-normal-white-24px {
  color: var(--white);
  font-family: var(--font-family-black_han_sans);
  font-size: var(--font-size-m2);
  font-style: normal;
  font-weight: 400;
}

.blackhansans-normal-shiraz-64px {
  color: var(--shiraz);
  font-family: var(--font-family-black_han_sans);
  font-size: var(--font-size-xxxl2);
  font-style: normal;
  font-weight: 400;
}

.blackhansans-normal-white-30px {
  color: var(--white);
  font-family: var(--font-family-black_han_sans);
  font-size: var(--font-size-l2);
  font-style: normal;
  font-weight: 400;
}
